import React from 'react';
import './top.css';
import UploadIcon from '@mui/icons-material/Upload';
import DownloadIcon from '@mui/icons-material/Download';
import AddIcon from '@mui/icons-material/Add';
import UndoIcon from '@mui/icons-material/Undo';
import RedoIcon from '@mui/icons-material/Redo';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import NoteAltIcon from '@mui/icons-material/NoteAlt';
import DocumentScannerIcon from '@mui/icons-material/DocumentScanner';
import HomeIcon from '@mui/icons-material/Home';
import OpenWithIcon from '@mui/icons-material/OpenWith';
import DeleteIcon from '@mui/icons-material/Delete';
import ProjectManager from '../canvas/ProjectManager';
import Tooltip from '@mui/material/Tooltip';
import RotateRightIcon from '@mui/icons-material/RotateRight'; // Right Move
import RotateLeftIcon from '@mui/icons-material/RotateLeft'; // Left Move
import FlipIcon from '@mui/icons-material/Flip'; // Mirror X
import FlipCameraAndroidIcon from '@mui/icons-material/FlipCameraAndroid'; // Mirror Y
import layoutEngine from '../libraries/layoutEngine';


const TopBar = () => {

  const [designName, setDesignName] = React.useState(ProjectManager.getDesignName());
  console.log(JSON.stringify(layoutEngine.getCells(), null, 2));
  const [activeTool, setActiveTool] = React.useState(ProjectManager.getTool());


  const exportGDS = async () => {
    const data = { cells: layoutEngine.getCells() };
    const res = await fetch('http://localhost:5000/generate-gds', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    });

    const blob = await res.blob();
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'layout.gds';
    a.click();
  };

  const selectTool = (toolName) => {
    ProjectManager.setTool(toolName);
    setActiveTool(toolName);
  };


  return (
    <header className="top-bar">
      <div className="logo-and-icons">
        <div className="logo">LOG<b style={{color:"green"}}>IC</b>KNOTS</div>

        

        <div className="icon-bar">
        <Tooltip title="Upload">
            <UploadIcon fontSize="small" />
          </Tooltip>
          <Tooltip title="Download">
            <DownloadIcon fontSize="small" onClick={exportGDS}/>
          </Tooltip>
          <Tooltip title="Add">
            <AddIcon fontSize="small" />
          </Tooltip>
          <Tooltip title="Undo">
            <UndoIcon fontSize="small" onClick={() => window.canvasEngine.undo()}/>
          </Tooltip>
          <Tooltip title="Redo">
            <RedoIcon fontSize="small" onClick={() => window.canvasEngine.redo()} />
          </Tooltip>
          <Tooltip title="Copy">
            <ContentCopyIcon fontSize="small" onClick={() => window.canvasEngine.copySelectedObjects()}/>
          </Tooltip>
          <Tooltip title="Paste">
            <NoteAltIcon fontSize="small" onClick={() => window.canvasEngine.pasteCopiedObjects()}/>
          </Tooltip>
          <Tooltip title="Scan Document">
            <DocumentScannerIcon fontSize="small" />
          </Tooltip>
          <Tooltip title="Home">
            <HomeIcon fontSize="small" />
          </Tooltip>
          <Tooltip title="Move">
            <OpenWithIcon fontSize="small" onClick={() => window.canvasEngine.enterMultiMoveMode()}/>
          </Tooltip>
          <Tooltip title="Delete">
            <DeleteIcon fontSize="small" onClick={() => window.canvasEngine.deleteSelectedObjects()}/>
          </Tooltip>
        </div>
      
        <div style={{
            display: 'flex',
            gap: '10px',
            alignItems: 'center',
            marginLeft: '20px',
            padding: '5px',
            border: '1px solid #444',
            borderRadius: '8px',
            backgroundColor: '#2b2b2b',
          }}>
          <Tooltip title="Right rotate">
            <RotateRightIcon fontSize="small" style={{ cursor: 'pointer' }} onClick={() => window.dispatchEvent(new CustomEvent('rotate-right'))}/>
          </Tooltip>
          <Tooltip title="Left rotate">
            <RotateLeftIcon fontSize="small" style={{ cursor: 'pointer' }} onClick={() => window.dispatchEvent(new CustomEvent('rotate-left'))} />
          </Tooltip>
          <Tooltip title="Mirror X">
            <FlipIcon fontSize="small" style={{ cursor: 'pointer' }} />
          </Tooltip>
          <Tooltip title="Mirror Y">
            <FlipCameraAndroidIcon fontSize="small" style={{ cursor: 'pointer' }} />
          </Tooltip>
        </div>
      </div>
      <div style={{ marginLeft: 'auto',marginRight: '7px',display: 'flex',alignItems: 'center' }}>
            <input type="text" value={designName} onChange={(e) => {ProjectManager.setDesignName(e.target.value);
            setDesignName(e.target.value);
                }}
    style={{padding: '10px',height:"10px",right:'0px',
      fontSize: '14px',
      borderRadius: '4px',
      border: '1px solid #888',
      backgroundColor: '#222',
      color: '#fff',
      outline: 'none',
      minWidth: '200px'
    }}
    placeholder="Enter Design Name"
            />
      </div>
    </header>
  );
};

export default TopBar;