// ✅ In-memory virtual layout manager for frontend

const layoutEngine = (() => {
  let cells = [
    {
      name: 'TOP',
      elements: [],
      instances: []
    }
  ];

  // ✅ Track the current active cell stack
  let activeCellStack = ["TOP"];

  const findCell = (name) => cells.find(cell => cell.name === name);
  const getCellByName = (name) => cells.find(cell => cell.name === name);

  const getCells = () => cells;

  const reset = () => {
    cells = [{ name: 'TOP', elements: [], instances: [] }];
    activeCellStack = ["TOP"];
  };

  const addCell = (name) => {
    if (!findCell(name)) {
      cells.push({ name, elements: [], instances: [] });
      return true;
    }
    return false;
  };

  const addRectangle = (cellName, x1, y1, x2, y2, layer = 1) => {
    const cell = findCell(cellName);
    if (cell) {
      cell.elements.push({ id: crypto.randomUUID(), type: 'rectangle', x1, y1, x2, y2, layer });
    }
  };

  const addText = (cellName, text, x, y, layer = 1) => {
    const cell = findCell(cellName);
    if (cell) {
      cell.elements.push({ id: crypto.randomUUID(), type: 'text', text, x, y, layer });
    }
  };

  

  const addInstance = (parentCell, refCellName, x, y, rotation = 0, external = false) => {
    const cell = findCell(parentCell);
    console.log("💥 Adding instance to cell:", cell);
  
    if (external && !findCell(refCellName)) {
      cells.push({ name: refCellName, elements: [], instances: [] });
      console.log(`📦 External cell '${refCellName}' created for hierarchy`);
    }
  
    if (cell) {
      const inst = {
        id: crypto.randomUUID(),
        ref: refCellName,
        x,
        y,
        rotation,
        external
      };
      cell.instances.push(inst);
      console.log("✅ Added instance:", inst);
    } else {
      console.error("❌ Cell not found:", parentCell);
    }
  };
  

  const deleteElement = (cellName, elementId) => {
    const cell = findCell(cellName);
    if (cell) {
      cell.elements = cell.elements.filter(el => el.id !== elementId);
    }
  };

  const deleteInstance = (cellName, instanceId) => {
    const cell = findCell(cellName);
    if (cell) {
      cell.instances = cell.instances.filter(inst => inst.id !== instanceId);
    }
  };

  const editElement = (cellName, elementId, updates) => {
    const cell = findCell(cellName);
    if (cell) {
      const el = cell.elements.find(e => e.id === elementId);
      if (el) Object.assign(el, updates);
    }
  };

  const editInstance = (cellName, instanceId, updates) => {
    const cell = findCell(cellName);
    if (cell) {
      const inst = cell.instances.find(i => i.id === instanceId);
      if (inst) Object.assign(inst, updates);
    }
  };

  // ✅ New methods for Show-As-Top support
  const getActiveCellName = () => {
    return activeCellStack[activeCellStack.length - 1];
  };

  const showAsTop = (cellName) => {
    // 🔁 If cell doesn't exist yet (external GDS), create it
    if (!findCell(cellName)) {
      cells.push({ name: cellName, elements: [], instances: [] });
      console.warn(`📦 Dynamically added cell for: ${cellName}`);
    }
    activeCellStack.push(cellName);
    console.log("🔁 Switched to cell:", cellName);
  };
  

  const exitShowAsTop = () => {
    if (activeCellStack.length > 1) {
      const popped = activeCellStack.pop();
      console.log("🔙 Exited cell:", popped, "➡️ Now in:", activeCellStack.at(-1));
    } else {
      console.warn("⚠️ Already at TOP, can't go back further.");
    }
  };

  return {
    getCells,
    reset,
    addCell,
    addRectangle,
    addText,
    addInstance,
    deleteElement,
    deleteInstance,
    editElement,
    editInstance,
    getCellByName,

    // ✅ Return newly added functions
    getActiveCellName,
    showAsTop,
    exitShowAsTop
  };
})();

export default layoutEngine;
