import React, { useEffect, useState } from 'react';
import hierarchyManager from '../libraries/HierarchyManager';
import selectionLibrary from '../libraries/SelectionLibrary';
import ProjectManager from '../canvas/ProjectManager';
import layoutEngine from '../libraries/layoutEngine';


const HierarchyPanel = () => {
  const [tree, setTree] = useState([]);
  const [cells, setCells] = useState(layoutEngine.getCells());
  const [gdsFiles, setGdsFiles] = useState([]);
  const [selectedGDS, setSelectedGDS] = useState('');
  const [expandedNodes, setExpandedNodes] = useState({});

  console.log("🧪 Instances in TOP cell:", layoutEngine.getCells().find(c => c.name === 'TOP').instances);
  console.log("🧪 layoutEngine cells:", layoutEngine.getCells());

  useEffect(() => {
    const fullTree = hierarchyManager.buildTree("TOP");
    console.log("📦 TREE FROM LIBRARY:", fullTree);
    setTree(fullTree);
  }, []);

  useEffect(() => {
    fetch("http://localhost:5000/list-gds")
      .then((res) => res.json())
      .then((data) => setGdsFiles(data.files))
      .catch((err) => console.error("❌ GDS fetch error", err));
  }, []);

 


  const toggleNode = (name) => {
    setExpandedNodes(prev => ({
      ...prev,
      [name]: !prev[name]
    }));
  };
  

  const handleSelect = (node) => {
    selectionLibrary.setSelection(node);
    ProjectManager.setSelectedInstanceCell(node.name);
  };

  const handleAddCell = () => {
    const newName = "CELL_" + Math.floor(Math.random() * 1000);
    const added = layoutEngine.addCell(newName);
    if (added) {
      setCells(layoutEngine.getCells());
      ProjectManager.setSelectedInstanceCell(newName);
      alert(`✅ '${newName}' added and selected for instance`);
    } else {
      alert("⚠️ Cell already exists");
    }
  };

  const buildInstanceTree = (cellName, visited = new Set()) => {
    if (visited.has(cellName)) return []; // 🔁 prevent infinite loop
    visited.add(cellName);
  
    const cell = layoutEngine.getCellByName(cellName);
    if (!cell) return [];
  
    return cell.instances.map((inst) => ({
      name: inst.ref,
      external: inst.external,
      children: buildInstanceTree(inst.ref, new Set(visited)), // 🔁 pass visited forward
    }));
  };
  

  const renderNode = (node, level = 0) => {
    const children = node.children || [];
    const hasChildren = children.length > 0;
    const isExpanded = expandedNodes[node.name];
  
    return (
      <div
        key={node.name + level}
        style={{ paddingLeft: `${level * 20}px`, marginBottom: '6px' }}
      >
        <div style={{ display: 'flex', alignItems: 'center' }}>
          {hasChildren ? (
            <span
              onClick={() => toggleNode(node.name)}
              style={{
                cursor: 'pointer',
                marginRight: '6px',
                color: '#0f0',
                fontSize: '14px',
                width: '16px',
                display: 'inline-block',
              }}
            >
              {isExpanded ? '⯆' : '⯈'}
            </span>
          ) : (
            <span style={{ width: '16px', display: 'inline-block' }} />
          )}
  
          <button
            style={{
              background: 'none',
              border: 'none',
              color: '#0af',
              cursor: 'pointer',
              fontSize: '14px',
              flexGrow: 1,
              textAlign: 'left',
            }}
            onClick={() => handleSelect(node)}
          >
            {node.name}
          </button>
  
          <button
            onClick={() => {
              layoutEngine.showAsTop(node.name);
              window.canvasEngine?.draw();
            }}
            style={{
              padding: '2px 6px',
              backgroundColor: '#333',
              color: '#0f0',
              border: '1px solid #444',
              borderRadius: '4px',
              fontSize: '12px',
              cursor: 'pointer',
              marginLeft: '6px',
            }}
          >
            👁
          </button>
        </div>
  
        {isExpanded && children.map((child) => renderNode(child, level + 1))}
      </div>
    );
  };
  
  
  

  return (
    <div>
      <h4 style={{ color: '#0f0', marginTop: '16px' }}>📦 Place Cell as Instance</h4>
      <h3 style={headerStyle}>Hierarchy</h3>

      <div style={{ marginBottom: '12px' }}>
        <h4 style={{ color: '#0f0' }}>📁 External GDS as Instance</h4>
        <select
          value={selectedGDS}
          onChange={(e) => {
            const file = e.target.value;
            setSelectedGDS(file);
            ProjectManager.setSelectedInstanceCell(file); // 👈 reuse existing setter
            alert(`📦 GDS instance selected: ${file}`);
          }}
          style={{
            width: '100%',
            padding: '6px',
            backgroundColor: '#111',
            color: '#fff',
            border: '1px solid #555',
            borderRadius: '4px',
          }}
        >
          <option value="">-- Select GDS file --</option>
          {gdsFiles.map((file, index) => (
            <option key={index} value={file}>
              {file}
            </option>
          ))}
        </select>
      </div>

      <button
        onClick={() => {
          const selected = ProjectManager.getSelectedInstanceCell();
          if (!selected) return alert("❌ Please select a cell to place as instance.");
          ProjectManager.setTool("instance");

          // 🔁 This syncs the tool with CanvasEngine
          window.dispatchEvent(new CustomEvent("tool-changed", { detail: "instance" }));

          alert(`🧲 Instance mode active: '${selected}' — click canvas to place`);
        }}
        style={{
          padding: '6px 10px',
          backgroundColor: '#2196f3',
          color: '#fff',
          border: 'none',
          borderRadius: '4px',
          cursor: 'pointer',
          fontWeight: 'bold',
          marginBottom: '20px',
        }}
      >
        📍 Place Instance
      </button>

      {tree.length === 0 ? (
  <div style={{ color: '#999', padding: '10px' }}>⚠️ No hierarchy available</div>
) : (
  renderNode(tree[0])
)}




    </div>
  );
};

const headerStyle = {
  fontSize: '16px',
  marginBottom: '10px',
  borderBottom: '1px solid #444',
  paddingBottom: '5px',
};

export default HierarchyPanel;
