let designName = "Untitled_Design";
let selectedInstanceCell = null; // ✅ NEW: store selected cell for instance placement
let currentTool = "select";

const ProjectManager = {
  getDesignName: () => designName,

  setDesignName: (name) => {
    designName = name;
    console.log("Design name set to:", designName);
  },

  // ✅ New Methods:
  getSelectedInstanceCell: () => selectedInstanceCell,

  setSelectedInstanceCell: (name) => {
    selectedInstanceCell = name;
    console.log("Selected cell for instance:", selectedInstanceCell);
  },
  getTool: () => currentTool,
  setTool: (toolName) => {
    currentTool = toolName;
    console.log("🛠️ Tool set to:", currentTool);
  }

  
};

export default ProjectManager;
