// src/components/StartModal.jsx
import React from 'react';

const StartModal = ({ onStartNew, onLoadJSON }) => {
  return (
    <div style={overlayStyle}>
      <div style={modalStyle}>
        <h2 style={{ marginBottom: '20px' }}>📐 Start VLSI Layout</h2>

        <button style={btnStyle} onClick={onStartNew}>
          ➕ Start New Layout
        </button>

        <label style={{ ...btnStyle, cursor: 'pointer', width: 'auto' }}>
          📂 Load Layout (JSON)
          <input
            type="file"
            accept=".json"
            onChange={onLoadJSON}
            style={{ display: 'none' }}
          />
        </label>
      </div>
    </div>
  );
};

const overlayStyle = {
  position: 'fixed',
  top: 0,
  left: 0,
  height: '100vh',
  width: '100vw',
  backgroundColor: 'rgba(0,0,0,0.8)',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  zIndex: 9999,
};

const modalStyle = {
  backgroundColor: '#1e1e1e',
  padding: '40px',
  borderRadius: '10px',
  textAlign: 'center',
  color: '#fff',
  minWidth: '300px',
};

const btnStyle = {
  padding: '14px 28px',
  backgroundColor: '#2196f3',
  border: 'none',
  borderRadius: '8px',
  marginBottom: '20px',
  color: '#fff',
  fontSize: '16px',
  display: 'block',
  width: '100%',
};

export default StartModal;
