// src/libraries/Label.js

export default class Label {
    constructor({ 
      id, 
      text = '', 
      x = 0, 
      y = 0, 
      layerId = null, 
      color = '#ffffff', 
      selected = false, 
      fontSize = 12, 
      rotation = 0 
    }) {
      this.id = id;   
      this.type = "label";         // Unique ID
      this.text = text;       // Text of the label
      this.x = x;             // X-coordinate
      this.y = y;             // Y-coordinate
      this.layerId = layerId; // Layer it belongs to
      this.color = color;     // Text color
      this.selected = selected; // Selected or not
      this.fontSize = fontSize;  // Font size
      this.rotation = rotation;  // Rotation angle (future)
    }
  }
  