// src/libraries/ToolManager.js

class ToolManager {
    constructor() {
      this.currentTool = "select"; // default tool
    }
  
    setTool(toolName) {
      this.currentTool = toolName;
      if (window.canvasEngine) {
        window.canvasEngine.currentTool = toolName;
        window.canvasEngine.isPanning = false;
        window.canvasEngine.isDrawing = false;
        window.canvasEngine.drawStart = null;
        // window.canvasEngine.selectedRects = []; // ✅ Optional: clear selection on tool change
      }
      console.log(`🛠️ Tool switched to: ${toolName}`);
    }
  
    getCurrentTool() {
      return this.currentTool;
    }
  }
  
  window.ToolManager = new ToolManager(); // ✅ Attach globally
  export default window.ToolManager;
  