let instances = {}; // key = layerId, value = array of rectangles

export function hexToRgba(color, alpha = 1) {
  if (!color) return 'rgba(0,0,0,0.4)'; // default fallback color

  if (!color.startsWith("#")) {
    const ctx = document.createElement("canvas").getContext("2d");
    ctx.fillStyle = color;
    color = ctx.fillStyle; // browser converts to hex
  }

  const bigint = parseInt(color.replace("#", ""), 16);
  const r = (bigint >> 16) & 255;
  const g = (bigint >> 8) & 255;
  const b = bigint & 255;
  return `rgba(${r}, ${g}, ${b}, ${alpha})`;
}



function deleteRectangle(layerId, rectId) {
  if (!instances[layerId]) return;
  instances[layerId] = instances[layerId].filter(rect => rect.id !== rectId);
}




const InstanceLibrary = {
  addRectangle: (layerId, x, y, width, height, options = {}) => {
    const rect = {
      id: options.id || Date.now(),
      type: "rectangle",
      x,
      y,
      width,
      height,
      selected: false,
      layerId,
      rotation: options.rotation || 0,
      layerNumber: options.layerNumber ?? 1,  // ✅ ADD THIS
  datatypeNumber: options.datatypeNumber ?? 0, 
      ...options
    };
  
    if (!instances[layerId]) instances[layerId] = [];
  
    // Optional: Prevent duplicate if same ID already exists
    console.log("🧱 addRectangle called:", layerId, x, y, options?.id);

    const isDuplicate = instances[layerId].some(r => r.id === rect.id);
    if (!isDuplicate) {
      instances[layerId].push(rect);

    }
  },
  
  getAllInstances: () => instances,
  
  updateColorByLayer(layerId, newColor) {
    const items = instances[layerId] || [];
    items.forEach(rect => {
      rect.fill = newColor; // or rect.color = newColor if using color
    });
  },
  
  setAllInstances: (newInstances) => {
    // Clear and replace the entire instance map
    instances = JSON.parse(JSON.stringify(newInstances));
  },
  

  getInstancesByLayer: (layerId) => instances[layerId] || [],





  drawSingle(ctx, rect, scale = 1) {
    ctx.beginPath();
    ctx.fillStyle = rect.fill || 'rgba(0,255,0,0.3)';
    ctx.strokeStyle = rect.stroke || 'green';
    ctx.lineWidth = 1 / scale;

    const cx = rect.x + rect.width / 2;
    const cy = rect.y + rect.height / 2;

    ctx.save();
    ctx.translate(cx, cy);
    ctx.rotate((rect.rotation || 0) * Math.PI / 180);
    ctx.translate(-cx, -cy);

    ctx.rect(rect.x, rect.y, rect.width, rect.height);
    ctx.fill();
    ctx.stroke();

    ctx.restore();
},

  drawAll: (ctx, offsetX, offsetY, scale, activeLayerId, previewRect) => {
    ctx.save();
    ctx.translate(offsetX, offsetY);
    ctx.scale(scale, scale);

    for (const layerId in instances) {
      const isActive = layerId === activeLayerId;
      const items = instances[layerId] || [];

      for (const inst of items) {
        ctx.beginPath();
        ctx.fillStyle = inst.fill;  // ✅ dim faded
        ctx.strokeStyle = inst.stroke;
        ctx.lineWidth = 1 / scale;
        ctx.save();
        const cx = inst.x + inst.width / 2;
        const cy = inst.y + inst.height / 2;

        ctx.translate(cx, cy);
        ctx.rotate((inst.rotation || 0) * Math.PI / 180); // degree → radian
        ctx.translate(-cx, -cy);

        ctx.rect(inst.x, inst.y, inst.width, inst.height);
        ctx.fill();
        ctx.stroke();
        ctx.restore();
 
        
      }
    }

    if (previewRect) {
      ctx.beginPath();
      ctx.setLineDash([4, 2]);
      ctx.strokeStyle = "#fff";
      ctx.lineWidth = 1 / scale;
      ctx.rect(previewRect.x, previewRect.y, previewRect.width, previewRect.height);
      ctx.stroke();
      ctx.setLineDash([]);
    }

    ctx.restore();
  },

  deleteRectangle,
};

export default InstanceLibrary;
