// src/canvas/instanceManager.js

export default class InstanceManager {
    constructor() {
      this.instances = [];
    }
  
    addInstance({ id, x, y, w, h, label = '', color = '#00f' }) {
      const instance = { id, x, y, w, h, label, color };
      this.instances.push(instance);
    }
  
    getAll() {
      return this.instances;
    }
  
    clearAll() {
      this.instances = [];
    }
  
    removeById(id) {
      this.instances = this.instances.filter(inst => inst.id !== id);
    }
  }
  